<template>
  <TableLayout :permissions="['system:loginLog:query']">
    <!--  -->
    <el-form ref="searchForm" slot="search-form" :model="searchForm" label-width="100px" inline>
      <el-form-item label="" prop="loginUsername">
        <el-input v-model="searchForm.loginUsername" placeholder="" @keypress.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="IP" prop="ip">
        <el-input v-model="searchForm.ip" placeholder="IP" @keypress.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="IP" prop="serverIp">
        <el-input v-model="searchForm.serverIp" placeholder="IP" @keypress.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="" prop="success">
        <el-select v-model="searchForm.success" placeholder="" clearable @change="search">
          <el-option value="true" label=""/>
          <el-option value="false" label=""/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="loginTime">
        <el-date-picker
          v-model="searchDateRange"
          type="datetimerange"
          range-separator=""
          value-format="yyyy-MM-dd HH:mm:ss"
          start-placeholder=""
          end-placeholder=""
          @change="handleSearchTimeChange"
        ></el-date-picker>
      </el-form-item>
      <section>
        <el-button type="primary" @click="search"></el-button>
        <el-button type="primary" :loading="isWorking.export" @click="exportExcel"></el-button>
        <el-button @click="reset"></el-button>
      </section>
    </el-form>
    <!--  -->
    <template v-slot:table-wrap>
      <el-table
        v-loading="isWorking.search"
        :data="tableData.list"
        stripe
        :default-sort="{prop: 'loginTime', order: 'descending'}"
        @sort-change="handleSortChange"
      >
        <el-table-column prop="loginUsername" label="" min-width="100px"></el-table-column>
        <el-table-column prop="ip" label="IP" min-width="120px"></el-table-column>
        <el-table-column prop="location" label="" min-width="160px"></el-table-column>
        <el-table-column prop="clientInfo" label="" min-width="160px"></el-table-column>
        <el-table-column prop="osInfo" label="" min-width="100px"></el-table-column>
        <el-table-column prop="platform" label="" min-width="100px"></el-table-column>
        <el-table-column prop="loginTime" label="" min-width="160px" sortable="custom" sort-by="LOGIN_TIME"></el-table-column>
        <el-table-column prop="systemVersion" label="" min-width="100px"></el-table-column>
        <el-table-column prop="serverIp" label="IP" min-width="120px"></el-table-column>
        <el-table-column prop="success" label="" min-width="100px">
          <template slot-scope="{row}">
            {{row.success | statusText}}
          </template>
        </el-table-column>
        <el-table-column prop="reason" label="" min-width="160px"></el-table-column>
      </el-table>
      <pagination
          @size-change="handleSizeChange"
          @current-change="handlePageChange"
          :pagination="tableData.pagination"
      ></pagination>
    </template>
  </TableLayout>
</template>

<script>
import BaseTable from '@/components/base/BaseTable'
import TableLayout from '@/layouts/TableLayout'
import Pagination from '@/components/common/Pagination'

export default {
  name: 'SystemLoginLog',
  extends: BaseTable,
  components: { TableLayout, Pagination },
  data () {
    return {
      // 
      searchDateRange: [],
      // 
      searchForm: {
        loginUsername: '',
        ip: '',
        serverIp: '',
        success: '',
        startTime: null,
        endTime: null
      }
    }
  },
  filters: {
    // 
    statusText (value) {
      if (value) {
        return ''
      }
      return ''
    }
  },
  methods: {
    // 
    handleSearchTimeChange (value) {
      this.searchForm.startTime = null
      this.searchForm.endTime = null
      if (value != null) {
        this.searchForm.startTime = value[0]
        this.searchForm.endTime = value[1]
      }
      this.search()
    }
  },
  created () {
    this.config({
      module: '',
      api: '/system/loginLog',
      'field.id': 'id',
      'field.main': 'id',
      sorts: [{
        property: 'LOGIN_TIME',
        direction: 'DESC'
      }]
    })
    this.search()
  }
}
</script>
