<template>
  <GlobalWindow
    :title="title"
    :visible.sync="visible"
    :confirm-working="isWorking"
    @confirm="confirm"
  >
    <el-form :model="form" ref="form" :rules="rules">
      <el-form-item label="" prop="parentId">
        <PositionSelect v-if="visible" v-model="form.parentId" placeholder="" :exclude-id="excludePositionId" clearable :inline="false"/>
      </el-form-item>
      <el-form-item label="" prop="code" required>
        <el-input v-model="form.code" placeholder="" v-trim maxlength="50"/>
      </el-form-item>
      <el-form-item label="" prop="name" required>
        <el-input v-model="form.name" placeholder="" v-trim maxlength="50"/>
      </el-form-item>
    </el-form>
  </GlobalWindow>
</template>

<script>
import BaseOpera from '@/components/base/BaseOpera'
import GlobalWindow from '@/components/common/GlobalWindow'
import PositionSelect from '@/components/common/PositionSelect'
export default {
  name: 'OperaPositionWindow',
  extends: BaseOpera,
  components: { PositionSelect, GlobalWindow },
  data () {
    return {
      // ID
      excludePositionId: null,
      // 
      form: {
        id: null,
        parentId: null,
        code: '',
        name: ''
      },
      // 
      rules: {
        code: [
          { required: true, message: '' }
        ],
        name: [
          { required: true, message: '' }
        ]
      }
    }
  },
  methods: {
    /**
     * @title 
     * @target 
     * @parent 
     * @positionList 
     */
    open (title, target, parent) {
      this.title = title
      this.visible = true
      // 
      if (target == null) {
        this.excludePositionId = null
        this.$nextTick(() => {
          this.$refs.form.resetFields()
          this.form.id = null
          this.form.parentId = parent == null ? null : parent.id
        })
        return
      }
      // 
      this.$nextTick(() => {
        this.excludePositionId = target.id
        for (const key in this.form) {
          this.form[key] = target[key]
        }
      })
    }
  },
  created () {
    this.config({
      api: '/system/position'
    })
  }
}
</script>
