<template>
  <TableLayout :permissions="['system:location:query']">
    <!--  -->
    <el-form ref="searchForm" slot="search-form" :model="searchForm" label-width="100px" inline>
      <el-form-item label="" prop="name">
        <el-input v-model="searchForm.name" placeholder="" @keypress.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="" prop="parentId">
        <LocationSelect :city-id.sync="searchForm.parentId" placeholder="" :level="2" clearable @change="search"/>
      </el-form-item>
      <el-form-item label="" prop="level">
        <el-select v-model="searchForm.level" placeholder="" clearable @change="search">
          <el-option value="1" label=""/>
          <el-option value="2" label=""/>
          <el-option value="3" label="/"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="areaCode">
        <el-input v-model="searchForm.areaCode" placeholder="" @keypress.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="" prop="postalCode">
        <el-input v-model="searchForm.postalCode" placeholder="" @keypress.enter.native="search"></el-input>
      </el-form-item>
      <section>
        <el-button type="primary" @click="search"></el-button>
        <el-button @click="reset"></el-button>
      </section>
    </el-form>
    <!--  -->
    <template v-slot:table-wrap>
      <ul class="toolbar" v-permissions="['system:location:create']">
        <li><el-button type="primary" @click="$refs.operaLocationWindow.open('')" icon="el-icon-plus" v-permissions="['system:location:create']"></el-button></li>
      </ul>
      <el-table
        v-loading="isWorking.search"
        :data="tableData.list"
        stripe
      >
        <el-table-column prop="name" label="" min-width="100px"></el-table-column>
        <el-table-column prop="shortName" label="" min-width="100px"></el-table-column>
        <el-table-column prop="fullName" label="" min-width="180px"></el-table-column>
        <el-table-column prop="pinyin" label="" min-width="100px"></el-table-column>
        <el-table-column prop="level" label="" min-width="80px">
          <template slot-scope="{row}">
            {{ row.level | levelText }}
          </template>
        </el-table-column>
        <el-table-column prop="areaCode" label="" min-width="100px"></el-table-column>
        <el-table-column prop="postalCode" label="" min-width="100px"></el-table-column>
        <el-table-column prop="firstLetter" label="" min-width="100px"></el-table-column>
        <el-table-column prop="disabled" label="" min-width="80px">
          <template slot-scope="{row}">
            <el-switch v-model="row.disabled" :active-value="false" :inactive-value="true" @change="switchDisabled(row)"/>
          </template>
        </el-table-column>
        <el-table-column prop="lng" label="" min-width="100px"></el-table-column>
        <el-table-column prop="lat" label="" min-width="100px"></el-table-column>
        <el-table-column
          v-if="containPermissions(['system:location:update', 'system:location:delete'])"
          label=""
          min-width="160"
          fixed="right"
        >
          <template slot-scope="{row}">
            <el-button type="text" @click="$refs.operaLocationWindow.open('', row)" icon="el-icon-edit" v-permissions="['system:location:update']"></el-button>
            <el-button type="text" v-if="row.level === 1" @click="$refs.operaLocationWindow.open('', null, row)" icon="el-icon-edit" v-permissions="['system:location:create']"></el-button>
            <el-button type="text" v-if="row.level === 2" @click="$refs.operaLocationWindow.open('/', null, row)" icon="el-icon-edit" v-permissions="['system:location:create']">/</el-button>
          </template>
        </el-table-column>
      </el-table>
      <pagination
        @size-change="handleSizeChange"
        @current-change="handlePageChange"
        :pagination="tableData.pagination"
      >
      </pagination>
    </template>
    <!-- / -->
    <OperaLocationWindow ref="operaLocationWindow" @success="handlePageChange"/>
  </TableLayout>
</template>

<script>
import BaseTable from '@/components/base/BaseTable'
import TableLayout from '@/layouts/TableLayout'
import Pagination from '@/components/common/Pagination'
import OperaLocationWindow from '@/components/system/location/OperaLocationWindow'
import LocationSelect from '@/components/common/LocationSelect'
import { updateStatus } from '@/api/system/location'

export default {
  name: 'SystemLocation',
  extends: BaseTable,
  components: { LocationSelect, TableLayout, Pagination, OperaLocationWindow },
  data () {
    return {
      // 
      searchForm: {
        parentId: null,
        level: null,
        name: '',
        areaCode: '',
        postalCode: ''
      }
    }
  },
  filters: {
    levelText (value) {
      if (value === 1) {
        return ''
      }
      if (value === 2) {
        return ''
      }
      if (value === 3) {
        return '/'
      }
      return ''
    }
  },
  methods: {
    // /
    switchDisabled (row) {
      if (!row.disabled) {
        this.__updateMenuStatus(row)
        return
      }
      this.$dialog.disableConfirm(` ${row.fullName} `)
        .then(() => {
          this.__updateMenuStatus(row)
        }).catch(() => {
          row.disabled = !row.disabled
        })
    },
    // 
    __updateMenuStatus (row) {
      updateStatus({
        id: row.id,
        disabled: row.disabled
      })
        .then(() => {
          this.$tip.apiSuccess('')
        })
        .catch(e => {
          row.disabled = !row.disabled
          this.$tip.apiFailed(e)
        })
    }
  },
  created () {
    this.config({
      module: '',
      api: '/system/location',
      'field.id': 'id',
      'field.main': 'fullName'
    })
    this.search()
  }
}
</script>
