<template>
  <TableLayout :permissions="['system:traceLog:query']">
    <!--  -->
    <el-form ref="searchForm" slot="search-form" :model="searchForm" label-width="100px" inline>
      <el-form-item label="" prop="userRealname">
        <el-input v-model="searchForm.userRealname" placeholder="" @keypress.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="" prop="operaModule">
        <el-input v-model="searchForm.operaModule" placeholder="" @keypress.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="" prop="requestUri">
        <el-input v-model="searchForm.requestUri" placeholder="" @keypress.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select v-model="searchForm.status" clearable @change="search">
          <el-option value="-1" label=""/>
          <el-option value="0" label=""/>
          <el-option value="1" label=""/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="exceptionLevel">
        <el-select v-model="searchForm.exceptionLevel" clearable @change="search">
          <el-option value="10" label=""/>
          <el-option value="5" label=""/>
          <el-option value="0" label=""/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="searchDateRange"
          type="datetimerange"
          range-separator=""
          value-format="yyyy-MM-dd HH:mm:ss"
          start-placeholder=""
          end-placeholder=""
          @change="handleSearchTimeChange"
        ></el-date-picker>
      </el-form-item>
      <section>
        <el-button type="primary" @click="search"></el-button>
        <el-button type="primary" :loading="isWorking.export" @click="exportExcel"></el-button>
        <el-button @click="reset"></el-button>
      </section>
    </el-form>
    <div slot="space" class="status-bar">
      <label class="status-normal"></label>
      <label class="status-warn">()</label>
      <label class="status-danger">()</label>
    </div>
    <!--  -->
    <template v-slot:table-wrap>
      <el-table
        v-loading="isWorking.search"
        :data="tableData.list"
        stripe
        :default-sort="{prop: 'operaTime', order: 'descending'}"
        :row-class-name="tableRowClassName"
        @sort-change="handleSortChange"
      >
        <el-table-column prop="operaModule" label="" min-width="100px"></el-table-column>
        <el-table-column prop="operaRemark" label="" min-width="100px"></el-table-column>
        <el-table-column prop="requestMethod" label="" min-width="80px"></el-table-column>
        <el-table-column prop="requestUri" label="" min-width="200px"></el-table-column>
        <el-table-column prop="status" label="" min-width="80px">
          <template slot-scope="{row}">
            {{row.status | statusText}}
          </template>
        </el-table-column>
        <el-table-column prop="requestParams" label="" min-width="80px">
          <template slot-scope="{row}">
            <ColumnDetail v-if="row.requestParams != null" :content="row.requestParams" :limit="0"/>
          </template>
        </el-table-column>
        <el-table-column prop="requestResult" label="" min-width="80px">
          <template slot-scope="{row}">
            <ColumnDetail v-if="row.requestResult != null" :content="row.requestResult"/>
          </template>
        </el-table-column>
        <el-table-column prop="exceptionLevel" label="" sortable="custom" sort-by="EXCEPTION_LEVEL" min-width="100px">
          <template slot-scope="{row}">
            {{row.exceptionLevel | exceptionLevelText}}
          </template>
        </el-table-column>
        <el-table-column prop="exceptionStack" label="" min-width="170px">
          <template slot-scope="{row}">
            <ColumnDetail v-if="row.exceptionStack != null" :content="row.exceptionStack" :button-type="getExceptionButtonType(row.exceptionLevel)"/>
          </template>
        </el-table-column>
        <el-table-column prop="operaSpendTime" label="(ms)" sortable="custom" sort-by="OPERA_SPEND_TIME" min-width="120px"></el-table-column>
        <el-table-column prop="userRealname" label="" min-width="100px"></el-table-column>
        <el-table-column prop="operaTime" label="" sortable="custom" sort-by="OPERA_TIME" min-width="140px"></el-table-column>
        <el-table-column prop="platform" label="" min-width="100px"></el-table-column>
        <el-table-column prop="systemVersion" label="" min-width="80px"></el-table-column>
        <el-table-column prop="serverIp" label="IP" min-width="100px"></el-table-column>
        <el-table-column prop="ip" label="IP" min-width="100px"></el-table-column>
        <el-table-column prop="clientInfo" label="" min-width="200px"></el-table-column>
        <el-table-column prop="osInfo" label="" min-width="100px"></el-table-column>
      </el-table>
      <pagination
        @size-change="handleSizeChange"
        @current-change="handlePageChange"
        :pagination="tableData.pagination"
      ></pagination>
    </template>
  </TableLayout>
</template>

<script>
import Pagination from '@/components/common/Pagination'
import TableLayout from '@/layouts/TableLayout'
import BaseTable from '@/components/base/BaseTable'
import ColumnDetail from '../../components/common/ColumnDetail'

export default {
  name: 'SystemTraceLog',
  extends: BaseTable,
  components: { ColumnDetail, TableLayout, Pagination },
  data () {
    return {
      // 
      searchDateRange: [],
      // 
      searchForm: {
        userRealname: '',
        operaModule: '',
        requestUri: '',
        status: null,
        exceptionLevel: null,
        startTime: null,
        endTime: null
      }
    }
  },
  filters: {
    // 
    statusText (value) {
      if (value === 1) {
        return ''
      }
      if (value === 0) {
        return ''
      }
      return ''
    },
    // 
    exceptionLevelText (value) {
      if (value == null) {
        return ''
      }
      if (value === 0) {
        return ''
      }
      if (value === 5) {
        return ''
      }
      if (value === 10) {
        return ''
      }
      return ''
    }
  },
  methods: {
    // 
    reset () {
      this.$refs.searchForm.resetFields()
      this.searchDateRange = []
      this.searchForm.startTime = null
      this.searchForm.endTime = null
      this.search()
    },
    // class
    tableRowClassName ({ row }) {
      if (row.exceptionLevel === 5 || row.status === -1) {
        return 'warning-log'
      } else if (row.exceptionLevel === 10) {
        return 'danger-log'
      }
      return ''
    },
    // 
    getExceptionButtonType (level) {
      if (level === 5) {
        return 'warning'
      }
      if (level === 10) {
        return 'danger'
      }
      return null
    },
    // 
    handleSearchTimeChange (value) {
      this.searchForm.startTime = null
      this.searchForm.endTime = null
      if (value != null) {
        this.searchForm.startTime = value[0]
        this.searchForm.endTime = value[1]
      }
      this.search()
    }
  },
  created () {
    this.config({
      api: '/system/traceLog',
      sorts: [{
        property: 'OPERA_TIME',
        direction: 'DESC'
      }]
    })
    this.search()
  }
}
</script>

