<template>
  <GlobalWindow
    :title="title"
    :visible.sync="visible"
    :confirm-working="isWorking"
    @confirm="confirm"
  >
    <el-form :model="form" ref="form" :rules="rules">
      <el-form-item label="" prop="parentId">
        <DepartmentSelect v-if="visible" ref="departmentSelect" placeholder="" v-model="form.parentId" :exclude-id="excludeDeptId" :inline="false"/>
      </el-form-item>
      <el-form-item label="" prop="code" required>
        <el-input v-model="form.code" placeholder="" v-trim maxlength="50"/>
      </el-form-item>
      <el-form-item label="" prop="name" required>
        <el-input v-model="form.name" placeholder="" v-trim maxlength="50"/>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="form.phone" placeholder="" v-trim maxlength="11"/>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="form.email" placeholder="" v-trim maxlength="200"/>
      </el-form-item>
    </el-form>
  </GlobalWindow>
</template>

<script>
import BaseOpera from '@/components/base/BaseOpera'
import GlobalWindow from '@/components/common/GlobalWindow'
import DepartmentSelect from '@/components/common/DepartmentSelect'
import { checkMobile, checkEmail } from '@/utils/form'
export default {
  name: 'OperaDepartmentWindow',
  extends: BaseOpera,
  components: { DepartmentSelect, GlobalWindow },
  data () {
    return {
      // ID
      excludeDeptId: null,
      // 
      form: {
        id: null,
        parentId: null,
        code: '',
        name: '',
        phone: '',
        email: ''
      },
      // 
      rules: {
        code: [
          { required: true, message: '' }
        ],
        name: [
          { required: true, message: '' }
        ],
        phone: [
          { validator: checkMobile }
        ],
        email: [
          { validator: checkEmail }
        ]
      }
    }
  },
  methods: {
    /**
     * @title 
     * @target 
     * @parent 
     * @departmentList 
     */
    open (title, target, parent) {
      this.title = title
      this.visible = true
      // 
      if (target == null) {
        this.excludeDeptId = null
        this.$nextTick(() => {
          this.$refs.form.resetFields()
          this.form.id = null
          this.form.parentId = parent == null ? null : parent.id
        })
        return
      }
      // 
      this.$nextTick(() => {
        this.excludeDeptId = target.id
        for (const key in this.form) {
          this.form[key] = target[key]
        }
      })
    }
  },
  created () {
    this.config({
      api: '/system/department'
    })
  }
}
</script>
