<template>
  <!-- / -->
  <GlobalWindow
    :title="title"
    :visible.sync="visible"
    :confirm-working="isWorking"
    @confirm="confirm"
  >
    <el-form :model="form" ref="form" :rules="rules">
      <el-form-item label="" prop="username" required>
        <el-input v-model="form.username" placeholder="" v-trim maxlength="50"/>
      </el-form-item>
      <el-form-item label="" prop="realname" required>
        <el-input v-model="form.realname" placeholder="" v-trim maxlength="50"/>
      </el-form-item>
      <el-form-item label="" prop="sex" required>
        <el-radio-group v-model="form.sex">
          <el-radio label="1"></el-radio>
          <el-radio label="0"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="avatar" required>
        <el-radio-group v-model="form.avatar" class="form-item-avatar">
          <el-radio label="/avatar/man.png" border><img src="/avatar/man.png" alt=""></el-radio>
          <el-radio label="/avatar/woman.png" border><img src="/avatar/woman.png" alt=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item v-if="form.id == null" label="" prop="password" required>
        <el-input v-model="form.password" type="password" placeholder="" maxlength="30" show-password/>
      </el-form-item>
            <el-form-item label="" prop="departmentId" required>
        <DepartmentSelect v-model="form.departmentId" placeholder="" :inline="false" clearable/>
      </el-form-item>
      <el-form-item label="" prop="positionId">
        <PositionSelect v-model="form.positionIds" placeholder="" :inline="false" :multiple="true" clearable/>
      </el-form-item>
      <el-form-item label="" prop="empNo">
        <el-input v-model="form.empNo" placeholder="" v-trim maxlength="50"/>
      </el-form-item>
      <el-form-item label="" prop="mobile">
        <el-input v-model="form.mobile" placeholder="" v-trim maxlength="11"/>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="form.email" placeholder="" v-trim maxlength="200"/>
      </el-form-item>
      <el-form-item label="" prop="birthday">
        <el-date-picker v-model="form.birthday" value-format="yyyy-MM-dd" placeholder=""/>
      </el-form-item>
    </el-form>
  </GlobalWindow>
</template>

<script>
import BaseOpera from '@/components/base/BaseOpera'
import GlobalWindow from '@/components/common/GlobalWindow'
import DepartmentSelect from '@/components/common/DepartmentSelect'
import PositionSelect from '@/components/common/PositionSelect'
import { checkMobile, checkEmail } from '@/utils/form'

export default {
  name: 'OperaUserWindow',
  extends: BaseOpera,
  components: { PositionSelect, DepartmentSelect, GlobalWindow },
  data () {
    return {
      // 
      form: {
        id: null,
        username: '', // 
        realname: '', // 
        empNo: '', // 
        departmentId: null, //  ID
        positionIds: [], //  ID
        avatar: '/avatar/man.png', // 
        password: '', // 
        mobile: '', // 
        email: '', // 
        sex: '1', // 
        birthday: '' // 
      },
      // 
      rules: {
        username: [
          { required: true, message: '' }
        ],
        realname: [
          { required: true, message: '' }
        ],
        password: [
          { required: true, message: '' }
        ],
        departmentId: [
          { required: true, message: '' }
        ],
        avatar: [
          { required: true, message: '' }
        ],
        sex: [
          { required: true, message: '' }
        ],
        mobile: [
          { validator: checkMobile }
        ],
        email: [
          { validator: checkEmail }
        ]
      }
    }
  },
  methods: {
    /**
     * @title 
     * @target 
     */
    open (title, target) {
      this.title = title
      this.visible = true
      // 
      if (target == null) {
        this.$nextTick(() => {
          this.$refs.form.resetFields()
          this.form.id = null
          this.form.departmentId = null
          this.form.positionIds = []
        })
        return
      }
      // 
      this.$nextTick(() => {
        for (const key in this.form) {
          this.form[key] = target[key]
        }
        this.form.departmentId = target.department == null ? null : target.department.id
        this.form.positionIds = target.positions == null ? [] : target.positions.map(p => p.id)
      })
    }
  },
  created () {
    this.config({
      api: '/system/user'
    })
  }
}
</script>

<style lang="scss" scoped>
.global-window {
  /deep/ .el-date-editor {
    width: 100%;
  }
  // 
  /deep/ .form-item-avatar {
    .el-radio.is-bordered {
      height: auto;
      padding: 6px;
      margin: 0 10px 0 0;
      .el-radio__input {
        display: none;
      }
      .el-radio__label {
        padding: 0;
        width: 48px;
        display: block;
        img {
          width: 100%;
        }
      }
    }
  }
}
</style>
