export default {
  install (Vue) {
    // 
    Vue.filter('sex', (value) => {
      if (value === '1') {
        return ''
      }
      if (value === '0') {
        return ''
      }
      return ''
    })
    // 
    Vue.filter('disabledText', (value) => {
      if (value) {
        return ''
      }
      return ''
    })
  }
}
