<template>
  <el-cascader
    v-if="visible"
    :props="props"
    :placeholder="placeholder"
    v-model="value"
    :clearable="clearable"
    @change="$emit('change')"
    @input="handleInput"
  ></el-cascader>
</template>

<script>
import { fetchByParentId } from '@/api/system/location'

export default {
  name: 'LocationSelect',
  props: {
    placeholder: {
      default: ''
    },
    level: {
      default: 3
    },
    clearable: {
      default: false
    },
    // 
    provinceId: {},
    // 
    cityId: {},
    // 
    areaId: {}
  },
  data () {
    const vm = this
    return {
      // cascader
      visible: true,
      // 
      value: [],
      // 
      props: {
        lazy: true,
        lazyLoad (node, resolve) {
          const { level } = node
          fetchByParentId(level === 0 ? -1 : node.value)
            .cache()
            .then(data => {
              resolve(data.map(item => {
                return {
                  label: item.name,
                  value: item.id,
                  leaf: level >= vm.level - 1
                }
              }))
            })
            .catch(e => {
              vm.$tip.apiFailed(e)
            })
        }
      }
    }
  },
  watch: {
    provinceId (newValue) {
      this.value[0] = newValue
      if (this.level === 1) {
        if (newValue == null) {
          this.value = []
        }
        this.__rebuild()
      }
    },
    cityId (newValue) {
      if (this.level >= 2) {
        this.value[1] = newValue
      }
      if (this.level === 2) {
        if (newValue == null) {
          this.value = []
        }
        this.__rebuild()
      }
    },
    areaId (newValue) {
      if (this.level >= 3) {
        this.value[2] = newValue
      }
      if (this.level === 3) {
        if (newValue == null) {
          this.value = []
        }
        this.__rebuild()
      }
    }
  },
  methods: {
    handleInput (values) {
      this.$emit('update:province-id', values[0])
      this.$emit('update:city-id', values[1])
      this.$emit('update:area-id', values[2])
    },
    // cascader
    __rebuild () {
      this.visible = false
      this.$nextTick(() => {
        this.visible = true
      })
    }
  }
}
</script>
