<template>
  <GlobalWindow
    :title="title"
    :visible.sync="visible"
    :confirm-working="isWorking"
    @confirm="confirm"
  >
    <el-form :model="form" ref="form" :rules="rules">
      <el-form-item label="" prop="name" required>
        <el-input v-model="form.name" maxlength="50" placeholder="" v-trim/>
      </el-form-item>
      <el-form-item label="" prop="shortName" required>
        <el-input v-model="form.shortName" maxlength="50" placeholder="" v-trim/>
      </el-form-item>
      <el-form-item label="" prop="fullName" required>
        <el-input v-model="form.fullName" maxlength="100" placeholder="" v-trim/>
      </el-form-item>
      <el-form-item label="" prop="pinyin" required>
        <el-input v-model="form.pinyin" maxlength="100" placeholder="" v-trim/>
      </el-form-item>
      <el-form-item label="" prop="areaCode">
        <el-input v-model="form.areaCode" maxlength="20" placeholder="" v-trim/>
      </el-form-item>
      <el-form-item label="" prop="postalCode">
        <el-input v-model="form.postalCode" maxlength="20" placeholder="" v-trim/>
      </el-form-item>
      <el-form-item label="" prop="firstLetter" required>
        <el-input v-model="form.firstLetter" maxlength="1" placeholder="" v-trim/>
      </el-form-item>
      <el-form-item label="" prop="lng">
        <el-input v-model="form.lng" maxlength="50" placeholder="" v-trim/>
      </el-form-item>
      <el-form-item label="" prop="lat">
        <el-input v-model="form.lat" maxlength="50" placeholder="" v-trim/>
      </el-form-item>
    </el-form>
  </GlobalWindow>
</template>

<script>
import BaseOpera from '@/components/base/BaseOpera'
import GlobalWindow from '@/components/common/GlobalWindow'
export default {
  name: 'OperaLocationWindow',
  extends: BaseOpera,
  components: { GlobalWindow },
  data () {
    return {
      // 
      form: {
        id: null,
        parentId: '',
        shortName: '',
        name: '',
        fullName: '',
        level: '',
        pinyin: '',
        areaCode: '',
        postalCode: '',
        firstLetter: '',
        lng: '',
        lat: ''
      },
      // 
      rules: {
        name: [
          { required: true, message: '' }
        ],
        shortName: [
          { required: true, message: '' }
        ],
        fullName: [
          { required: true, message: '' }
        ],
        pinyin: [
          { required: true, message: '' }
        ],
        firstLetter: [
          { required: true, message: '' }
        ]
      }
    }
  },
  methods: {
    /**
     * @title 
     * @parent ID
     * @target 
     */
    open (title, target, parent) {
      this.title = title
      this.visible = true
      // 
      if (target == null) {
        this.$nextTick(() => {
          this.$refs.form.resetFields()
          this.form.id = null
          this.form.parentId = parent == null ? null : parent.id
          this.form.level = parent == null ? 1 : parent.level + 1
        })
        return
      }
      // 
      this.$nextTick(() => {
        for (const key in this.form) {
          this.form[key] = target[key]
        }
      })
    }
  },
  created () {
    this.config({
      api: '/system/location',
      'field.id': 'id'
    })
  }
}
</script>
