<template>
  <GlobalWindow
    :title="title"
    :visible.sync="visible"
    :confirm-working="isWorking"
    @confirm="confirm"
  >
    <el-form :model="form" ref="form" :rules="rules">
      <el-form-item label="" prop="code" required>
        <el-input v-model="form.code" placeholder="" v-trim maxlength="50"/>
      </el-form-item>
      <el-form-item label="" prop="name" required>
        <el-input v-model="form.name" placeholder="" v-trim maxlength="50"/>
      </el-form-item>
      <el-form-item label="" prop="remark">
        <el-input v-model="form.remark" placeholder="" type="textarea" :rows="3" v-trim maxlength="500"/>
      </el-form-item>
    </el-form>
  </GlobalWindow>
</template>

<script>
import BaseOpera from '@/components/base/BaseOpera'
import GlobalWindow from '@/components/common/GlobalWindow'
export default {
  name: 'OperaPermissionWindow',
  extends: BaseOpera,
  components: { GlobalWindow },
  data () {
    return {
      // 
      originPermissionCode: '',
      // 
      form: {
        id: null,
        code: '',
        name: '',
        remark: ''
      },
      // 
      rules: {
        code: [
          { required: true, message: '' }
        ],
        name: [
          { required: true, message: '' }
        ]
      }
    }
  },
  methods: {
    open (title, target) {
      this.title = title
      this.visible = true
      // 
      if (target == null) {
        this.$nextTick(() => {
          this.$refs.form.resetFields()
          this.form[this.configData['field.id']] = null
        })
        return
      }
      // 
      this.$nextTick(() => {
        this.originPermissionCode = target.code
        for (const key in this.form) {
          this.form[key] = target[key]
        }
      })
    },
    confirm () {
      if (this.form.id == null || this.form.id === '') {
        this.__confirmCreate()
        return
      }
      if (this.originPermissionCode === this.form.code) {
        this.__confirmEdit()
        return
      }
      // 
      this.$dialog.confirm('', '', {
        confirmButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.__confirmEdit()
        })
    }
  },
  created () {
    this.config({
      api: '/system/permission'
    })
  }
}
</script>
