import { MessageBox } from 'element-ui'

export default {
  ...MessageBox,
  // 
  deleteConfirm (message) {
    return MessageBox.confirm(message, '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    })
  },
  // 
  disableConfirm (message) {
    return MessageBox.confirm(message, '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    })
  },
  // 
  exportConfirm (message) {
    return MessageBox.confirm(message, '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    })
  }
}
